import React from "react";
import "./secondsec.css";
import Mi from "..//image/Mi.jfif";

export default function Secondsec() {
  return (
    <div id ="aboutme" className="Container2">
      <div className="Container2-1">
        <div className="Img-Div">
          <img className="Mi" src={Mi} alt="" />
        </div>
        <div className="Text-Div">
          <div className="Text-Div-2">
            <h3 className="laranja">Sobre mim</h3>
            <p>
              Atuo como psicloga (CRP 05/53128) e como psicoterapeuta corporal
              desde 2016, tendo experincia clnica com adolescentes e adultos.
              Meu trabalho e minha escuta so orientados pela perspectiva
              corporal desde o incio da minha trajetria na psicologia clnica.
            </p>
            <p>
              Sou formada em Psicologia pela Universidade Federal Fluminense
              (UFF) e em Terapia Corporal Reichiana pelo Centro de Anlise do
              Movimento Vivo (AMV). Minha base  a terapia corporal de
              orientao reichiana, que tem como fundador Wilhelm Reich, mas
              conto tambm com o estudo de comentadores mais atuais de sua obra,
              alm de buscar conhecimento de reas anlogas, como a Experincia
              Somtica (SE), a Psicologia Social e estudos sobre gnero e
              sexualidade.
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}
